import { motion } from 'framer-motion';
import { cn } from '@/lib/utils';
import { useGameStore } from '@/lib/store';

interface CellProps {
    r: number;
    c: number;
    val: number;
    initial: boolean;
}

export const Cell = ({ r, c, val, initial }: CellProps) => {
    const { selectedCell, selectCell, board, solvedBoard, initialBoard, isNotesMode, notes, mistakes } = useGameStore();

    const isSelected = selectedCell?.r === r && selectedCell?.c === c;
    const isRelated = selectedCell && (selectedCell.r === r || selectedCell.c === c ||
        (Math.floor(selectedCell.r / 3) === Math.floor(r / 3) && Math.floor(selectedCell.c / 3) === Math.floor(c / 3)));

    const isError = !initial && val !== 0 && val !== solvedBoard[r][c];

    // Get notes for this cell
    const cellNotes = [];
    if (val === 0) {
        for (let n = 1; n <= 9; n++) {
            if (notes.has(`${r},${c},${n}`)) {
                cellNotes.push(n);
            }
        }
    }

    const handleClick = () => {
        selectCell(r, c);
    };

    return (
        <motion.div
            whileTap={{ scale: 0.95 }}
            onClick={handleClick}
            className={cn(
                "relative flex items-center justify-center w-full aspect-square border-r border-b border-gray-200 dark:border-gray-700 cursor-pointer transition-colors duration-150",
                "text-2xl sm:text-3xl font-medium select-none",
                r % 3 === 2 && r !== 8 && "border-b-2 border-b-primary-500",
                c % 3 === 2 && c !== 8 && "border-r-2 border-r-primary-500",
                isSelected && "bg-primary-200 dark:bg-primary-800 ring-2 ring-primary-500 z-10",
                !isSelected && isRelated && "bg-primary-50 dark:bg-slate-800",
                isError && "text-red-500 bg-red-50 dark:text-red-400 dark:bg-red-900/20",
                initial ? "text-slate-900 dark:text-slate-100 font-bold" : "text-primary-600 dark:text-primary-400"
            )}
        >
            {val !== 0 ? (
                <span className={isError ? "animate-pulse" : ""}>{val}</span>
            ) : (
                <div className="grid grid-cols-3 gap-[1px] w-full h-full p-0.5 pointer-events-none">
                    {cellNotes.map(n => (
                        <div key={n} className="flex items-center justify-center text-[8px] sm:text-[10px] text-slate-500 dark:text-slate-400 leading-none">
                            {n}
                        </div>
                    ))}
                </div>
            )}
        </motion.div>
    );
};
