import { useGameStore } from '@/lib/store';
import { Undo, Eraser, Pencil, Lightbulb, RotateCcw } from 'lucide-react';
import { motion } from 'framer-motion';
import { cn } from '@/lib/utils';

export const GameControls = () => {
    const { undo, setCellValue, toggleNotesMode, isNotesMode, resetGame, mistakes, maxMistakes } = useGameStore();

    const controls = [
        { icon: Undo, label: 'Undo', action: undo },
        { icon: Eraser, label: 'Erase', action: () => setCellValue(0) },
        {
            icon: Pencil,
            label: 'Notes',
            action: toggleNotesMode,
            active: isNotesMode,
            activeClass: "bg-primary-500 text-white border-primary-600 hover:bg-primary-600"
        },
        // Hint could be added here
    ];

    return (
        <div className="flex flex-col gap-4 w-full max-w-md">
            <div className="flex items-center justify-between text-slate-600 dark:text-slate-400 px-2 font-medium">
                <span>Mistakes: <span className={cn(mistakes > 0 ? "text-red-500" : "")}>{mistakes}</span>/{maxMistakes}</span>
                {/* Timer would go here */}
            </div>

            <div className="flex justify-between gap-2">
                {controls.map((item, idx) => (
                    <motion.button
                        key={idx}
                        whileTap={{ scale: 0.95 }}
                        onClick={item.action}
                        className={cn(
                            "flex flex-col items-center justify-center flex-1 h-16 rounded-xl gap-1 transition-all",
                            "bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 shadow-sm text-slate-700 dark:text-slate-300",
                            "hover:bg-slate-50 dark:hover:bg-slate-700",
                            item.active && item.activeClass
                        )}
                    >
                        <item.icon size={20} />
                        <span className="text-xs">{item.label}</span>
                    </motion.button>
                ))}
                <motion.button
                    whileTap={{ scale: 0.95 }}
                    onClick={resetGame}
                    className="flex flex-col items-center justify-center flex-1 h-16 rounded-xl gap-1 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 shadow-sm text-slate-700 dark:text-slate-300 hover:bg-slate-50 dark:hover:bg-slate-700 transition-all"
                >
                    <RotateCcw size={20} />
                    <span className="text-xs">Reset</span>
                </motion.button>
            </div>
        </div>
    );
};
