import { useGameStore } from '@/lib/store';
import { motion } from 'framer-motion';
import { cn } from '@/lib/utils';

export const Numpad = () => {
    const { setCellValue, notes } = useGameStore();

    return (
        <div className="grid grid-cols-3 sm:flex sm:flex-row gap-2 mt-6 w-full max-w-md">
            {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((num) => (
                <motion.button
                    key={num}
                    whileTap={{ scale: 0.9 }}
                    onClick={() => setCellValue(num)}
                    className={cn(
                        "flex items-center justify-center h-12 sm:h-14 sm:flex-1 rounded-xl text-2xl font-semibold transition-colors",
                        "bg-white dark:bg-slate-800 text-primary-600 dark:text-primary-400 shadow-md border border-slate-200 dark:border-slate-700",
                        "hover:bg-primary-50 dark:hover:bg-slate-700 hover:border-primary-200 dark:hover:border-primary-900"
                    )}
                >
                    {num}
                </motion.button>
            ))}
        </div>
    );
};
