"use client";

import { Board } from "@/components/game/Board";
import { GameControls } from "@/components/game/GameControls";
import { Numpad } from "@/components/game/Numpad";
import { Timer } from "@/components/game/Timer";
import { Lobby } from "@/components/layout/Lobby";
import { useGameStore } from "@/lib/store";
import { useEffect } from "react";
import { Users, User, Bot } from 'lucide-react';

export default function Home() {
  const { startGame, difficulty, status, mode, setMultiplayerState, opponentName, opponentProgress } = useGameStore();

  useEffect(() => {
    // Start single player by default only if not already set
    if (status === 'idle' && mode === 'single') {
      startGame(difficulty);
    }
  }, []);

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4 bg-slate-50 dark:bg-slate-950 font-sans transition-colors">
      <div className="w-full max-w-6xl flex flex-col lg:flex-row items-center justify-center gap-8 lg:gap-16">

        {/* Sidebar / Mode Selection */}
        <div className="flex lg:flex-col gap-4 mb-4 lg:mb-0">
          <button
            onClick={() => startGame('Medium', 'single')}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${mode === 'single' ? 'bg-primary-600 text-white' : 'bg-white dark:bg-slate-800 text-slate-700 dark:text-slate-300'}`}
          >
            <User size={20} />
            <span className="hidden sm:inline">Single</span>
          </button>
          <button
            onClick={() => setMultiplayerState({ mode: 'pvp', status: 'idle' })}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${mode === 'pvp' ? 'bg-accent-600 text-white' : 'bg-white dark:bg-slate-800 text-slate-700 dark:text-slate-300'}`}
          >
            <Users size={20} />
            <span className="hidden sm:inline">PvP</span>
          </button>
          <button
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors opacity-50 cursor-not-allowed bg-white dark:bg-slate-800 text-slate-700 dark:text-slate-300`}
            title="Coming Soon"
          >
            <Bot size={20} />
            <span className="hidden sm:inline">Bot</span>
          </button>
        </div>

        {mode === 'pvp' && status !== 'playing' && status !== 'won' && status !== 'lost' ? (
          <Lobby />
        ) : (
          <>
            {/* Header / Title Area (Mobile) */}
            <div className="lg:hidden w-full max-w-md flex justify-between items-center mb-4">
              <h1 className="text-3xl font-bold bg-gradient-to-r from-primary-600 to-accent-600 bg-clip-text text-transparent">
                Sudoku
              </h1>
              <div className="text-sm font-medium px-3 py-1 rounded-full bg-primary-100 text-primary-700 dark:bg-primary-900 dark:text-primary-300">
                {difficulty}
              </div>
            </div>

            {/* Game Board */}
            <div className="relative">
              <Board />

              {/* Shadow Board / Opponent Progress */}
              {mode === 'pvp' && (
                <div className="absolute -top-12 left-0 right-0 flex items-center justify-between px-4 py-2 bg-white/80 dark:bg-slate-900/80 backdrop-blur rounded-lg border border-primary-200 dark:border-primary-900">
                  <span className="font-bold text-slate-700 dark:text-slate-200">{opponentName || 'Opponent'}</span>
                  <div className="w-32 h-2 bg-slate-200 dark:bg-slate-700 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-red-500 transition-all duration-500"
                      style={{ width: `${opponentProgress}%` }}
                    />
                  </div>
                </div>
              )}
            </div>

            {/* Controls Area */}
            <div className="flex flex-col items-center w-full max-w-md gap-6">
              <div className="hidden lg:flex w-full justify-between items-center mb-2">
                <h1 className="text-4xl font-bold bg-gradient-to-r from-primary-600 to-accent-600 bg-clip-text text-transparent">
                  Sudoku
                </h1>
                <div className="flex items-center gap-4">
                  <Timer />
                  <div className="text-sm font-medium px-3 py-1 rounded-full bg-primary-100 text-primary-700 dark:bg-primary-900 dark:text-primary-300">
                    {difficulty}
                  </div>
                </div>
              </div>

              <div className="lg:hidden flex justify-center w-full -mt-2 mb-2">
                <Timer />
              </div>

              <GameControls />
              <Numpad />

              {/* Difficulty Selector (Only in Single Player) */}
              {mode === 'single' && (
                <div className="flex gap-2 w-full mt-4">
                  {(['Easy', 'Medium', 'Hard', 'Expert'] as const).map(diff => (
                    <button
                      key={diff}
                      onClick={() => startGame(diff)}
                      className={`flex-1 py-2 text-sm rounded-lg border transition-colors ${difficulty === diff
                          ? 'bg-primary-600 text-white border-primary-600'
                          : 'bg-white dark:bg-slate-800 text-slate-700 dark:text-slate-300 border-slate-200 dark:border-slate-700 hover:bg-slate-50 dark:hover:bg-slate-700'
                        }`}
                    >
                      {diff}
                    </button>
                  ))}
                </div>
              )}
            </div>
          </>
        )}

        {/* Win/Loss Overlay */}
        {(status === 'won' || status === 'lost') && (
          <div className="absolute inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm">
            <div className="bg-white dark:bg-slate-900 p-8 rounded-2xl shadow-2xl text-center max-w-sm w-full mx-4 border border-white/20">
              <h2 className="text-3xl font-bold mb-4 bg-gradient-to-r from-primary-600 to-accent-600 bg-clip-text text-transparent">
                {status === 'won' ? 'Puzzle Solved!' : 'Game Over'}
              </h2>
              <p className="text-slate-600 dark:text-slate-400 mb-6">
                {status === 'won'
                  ? `Great job! You finished in ${Math.floor(useGameStore.getState().timer / 60)}m ${useGameStore.getState().timer % 60}s`
                  : 'Too many mistakes. Try again!'}
              </p>
              <button
                onClick={() => startGame(difficulty)}
                className="w-full py-3 rounded-xl bg-primary-600 hover:bg-primary-700 text-white font-semibold transition-colors shadow-lg shadow-primary-500/30"
              >
                New Game
              </button>
            </div>
          </div>
        )}
      </div>
    </main>
  );
}
