import { useState } from 'react';
import { useGameStore } from '@/lib/store';
import { createRoom, joinRoom, subscribeToRoom } from '@/lib/firebase/rooms';
import { motion } from 'framer-motion';

export const Lobby = () => {
    const { setMultiplayerState, startGame } = useGameStore();
    const [name, setName] = useState('');
    const [roomIdInput, setRoomIdInput] = useState('');
    const [createdRoomId, setCreatedRoomId] = useState<string | null>(null);
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(false);

    const handleCreate = async () => {
        if (!name) {
            setError("Please enter your name");
            return;
        }
        setLoading(true);
        try {
            const id = await createRoom(name, 'Medium');
            setCreatedRoomId(id);
            setMultiplayerState({ roomId: id, playerId: name, mode: 'pvp', status: 'idle' });

            // Listen for opponent
            subscribeToRoom(id, (room) => {
                if (room.status === 'playing') {
                    setMultiplayerState({
                        status: 'playing',
                        board: room.initialBoard,
                        initialBoard: room.initialBoard,
                        solvedBoard: room.solvedBoard,
                        difficulty: room.difficulty
                    });

                    // Find opponent name
                    const opponent = Object.values(room.players || {}).find(p => p.name !== name);
                    if (opponent) {
                        setMultiplayerState({ opponentName: opponent.name });
                    }
                }
            });

        } catch (e: any) {
            setError(e.message);
        } finally {
            setLoading(false);
        }
    };

    const handleJoin = async () => {
        if (!name || !roomIdInput) return;
        setLoading(true);
        try {
            await joinRoom(roomIdInput, name);
            setMultiplayerState({ roomId: roomIdInput, playerId: name, mode: 'pvp' });

            // Listen
            subscribeToRoom(roomIdInput, (room) => {
                if (room.status === 'playing') {
                    setMultiplayerState({
                        status: 'playing',
                        board: room.initialBoard,
                        initialBoard: room.initialBoard,
                        solvedBoard: room.solvedBoard,
                        difficulty: room.difficulty
                    });

                    const opponent = Object.values(room.players || {}).find(p => p.name !== name);
                    if (opponent) {
                        setMultiplayerState({ opponentName: opponent.name });
                    }
                }
            });

        } catch (e: any) {
            setError(e.message);
        } finally {
            setLoading(false);
        }
    };

    if (createdRoomId) {
        return (
            <div className="flex flex-col items-center justify-center p-8 bg-white dark:bg-slate-800 rounded-2xl shadow-xl w-full max-w-md border border-slate-200 dark:border-slate-700">
                <h2 className="text-2xl font-bold mb-4 bg-gradient-to-r from-primary-600 to-accent-600 bg-clip-text text-transparent">Room Created!</h2>
                <div className="bg-slate-100 dark:bg-slate-900 p-6 rounded-xl mb-6 text-center w-full">
                    <p className="text-sm text-slate-500 mb-2 font-medium">Share this Code:</p>
                    <p className="text-4xl font-mono font-bold tracking-wider select-all cursor-pointer text-slate-800 dark:text-slate-200">{createdRoomId}</p>
                </div>
                <div className="flex items-center gap-3 text-primary-600 dark:text-primary-400 animate-pulse font-medium">
                    <div className="w-2 h-2 rounded-full bg-primary-600 dark:bg-primary-400 animate-bounce" />
                    <span>Waiting for opponent...</span>
                </div>
            </div>
        );
    }

    return (
        <div className="flex flex-col gap-6 w-full max-w-sm mx-auto p-6 bg-white dark:bg-slate-800 rounded-2xl shadow-xl border border-slate-200 dark:border-slate-700">
            <h2 className="text-2xl font-bold text-center bg-gradient-to-r from-primary-600 to-accent-600 bg-clip-text text-transparent">PvP Lobby</h2>

            <input
                type="text"
                placeholder="Your Name"
                value={name}
                onChange={e => setName(e.target.value)}
                className="w-full px-4 py-3 rounded-xl border border-slate-200 dark:border-slate-700 bg-slate-50 dark:bg-slate-900 focus:ring-2 focus:ring-primary-500 outline-none transition-all"
            />

            <div className="flex flex-col gap-4">
                <button
                    onClick={handleCreate}
                    disabled={loading || !name}
                    className="w-full py-3 bg-primary-600 text-white rounded-xl font-semibold hover:bg-primary-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors shadow-lg shadow-primary-500/20"
                >
                    {loading ? 'Creating...' : 'Create Room'}
                </button>

                <div className="flex items-center gap-4">
                    <div className="h-[1px] bg-slate-200 dark:bg-slate-700 flex-1" />
                    <span className="text-slate-400 text-sm font-medium">OR</span>
                    <div className="h-[1px] bg-slate-200 dark:bg-slate-700 flex-1" />
                </div>

                <div className="flex gap-2">
                    <input
                        type="text"
                        placeholder="Room Code"
                        value={roomIdInput}
                        onChange={e => setRoomIdInput(e.target.value)}
                        className="flex-1 px-4 py-3 rounded-xl border border-slate-200 dark:border-slate-700 bg-slate-50 dark:bg-slate-900 focus:ring-2 focus:ring-primary-500 outline-none transition-all"
                    />
                    <button
                        onClick={handleJoin}
                        disabled={loading || !name || !roomIdInput}
                        className="px-6 py-3 bg-slate-800 dark:bg-slate-700 text-white rounded-xl font-semibold hover:bg-slate-900 dark:hover:bg-slate-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                    >
                        {loading ? '...' : 'Join'}
                    </button>
                </div>
            </div>
            {error && <p className="text-red-500 text-sm text-center font-medium bg-red-50 dark:bg-red-900/20 py-2 rounded-lg">{error}</p>}
        </div>
    );
};
